@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;900&display=swap";
html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  height: 100vh;
  background-image: radial-gradient(circle, #1a488e 0, #153c78 100%);
  font-family: Montserrat, sans-serif;
}

.main-title {
  color: #153c78;
  text-align: center;
  margin: 0;
  font-size: 5rem;
  font-weight: 900;
}

.container {
  max-width: 991px;
  grid-template-columns: 300px minmax(auto, 400px);
  justify-content: center;
  align-items: center;
  gap: 28px;
  margin: 28px auto;
  padding: 16px;
  display: grid;
}

@media (max-width: 767px) {
  .container {
    grid-template-columns: minmax(auto, 400px);
  }
}

.lists {
  color: #e3e3e3;
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.lists__form {
  gap: 8px;
  padding: 0;
  display: flex;
}

.lists__input {
  border: unset;
  color: #e3e3e3;
  outline: unset;
  background-color: #0000;
  border-bottom: 1px solid #e3e3e3;
  padding: 4px 0;
  transition: box-shadow .3s;
}

.lists__input:focus {
  box-shadow: 0 1px #e3e3e3;
}

.lists__input::-moz-placeholder {
  color: #a9a9a9;
}

.lists__input:-ms-input-placeholder {
  color: #a9a9a9;
}

.lists__input::placeholder {
  color: #a9a9a9;
}

.lists__submit {
  border: unset;
  color: #e3e3e3;
  background-color: #0000;
  order: -1;
  padding: 0;
  font-weight: bold;
}

.lists__title {
  margin: 16px 0;
}

.lists__list {
  margin: 0;
  padding: 0;
  list-style: circle;
}

.lists__item {
  cursor: pointer;
}

.lists__item--active {
  font-weight: bold;
  list-style-type: disc;
}

.tasks {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.tasks__form {
  gap: 8px;
  padding: 0 16px 16px;
  display: flex;
}

.tasks__input {
  border: unset;
  color: #2a2a2a;
  outline: unset;
  background-color: #0000;
  border-bottom: 1px solid #2a2a2a;
  padding: 4px 0;
  transition: box-shadow .3s;
}

.tasks__input:focus {
  box-shadow: 0 1px #2a2a2a;
}

.tasks__input::-moz-placeholder {
  color: #a9a9a9;
}

.tasks__input:-ms-input-placeholder {
  color: #a9a9a9;
}

.tasks__input::placeholder {
  color: #a9a9a9;
}

.tasks__submit {
  border: unset;
  color: #2a2a2a;
  background-color: #0000;
  order: -1;
  padding: 0;
  font-weight: bold;
}

.tasks--hidden {
  display: none;
}

.tasks__container {
  color: #2a2a2a;
  background-color: #e3e3e3;
  border-radius: 4px;
  flex-direction: column;
  gap: 16px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 5px 15px #2a2a2a;
}

.tasks__header {
  background-color: #a9a9a9;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  display: flex;
}

.tasks__title {
  margin: 0;
}

.tasks__info {
  font-size: 14px;
  font-weight: bold;
}

.tasks__list {
  flex-direction: column;
  gap: 8px;
  margin: 0;
  padding: 0 16px;
  list-style: none;
  display: flex;
}

.tasks__item {
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 8px;
  display: grid;
}

.tasks__item:hover .tasks__delete {
  display: block;
}

.tasks__content--done {
  text-decoration: line-through;
}

.tasks__toggle {
  height: 20px;
  width: 20px;
  border: unset;
  color: #e3e3e3;
  background-color: #153c78;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 12px;
  transition: filter .3s;
  display: flex;
}

.tasks__toggle:hover {
  filter: brightness(110%);
}

.tasks__toggle:active {
  filter: brightness(120%);
}

.tasks__delete {
  border: unset;
  color: #b05f66;
  background-color: #0000;
  font-size: 14px;
  transition: filter .3s;
  display: none;
}

.tasks__delete:hover {
  filter: brightness(90%);
}

.tasks__delete:active {
  filter: brightness(80%);
}

.tasks__buttons {
  justify-content: space-evenly;
  gap: 8px;
  display: flex;
}

@media (max-width: 359px) {
  .tasks__buttons {
    flex-direction: column;
  }
}

.tasks__button {
  border: unset;
  background: unset;
  color: #e3e3e3;
  cursor: pointer;
  font-size: 14px;
  transition: filter .3s;
}

.tasks__button:hover {
  filter: brightness(110%);
}

.tasks__button:active {
  filter: brightness(120%);
}

